:root {
  --color-text-header-l: #595959;
  --color-text-header-d: #b7b7b7;
  --color-text-description: #f8f8f8;
  --color-black: #111;
  --color-second-black: #282828;
  --color-white: #fff;
  --color-text-button: #f87719;
  --color-button: linear-gradient( 141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --color-button-hover: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  --transition-default: 250ms cubic-bezier(.4, 0, .2, 1);
  --shadow: 1px 1px 14px rgba(1, 1, 1, .25);
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  overflow-x: hidden;
}

*, :before, :after {
  box-sizing: inherit;
}

:active, :hover, :focus {
  outline-offset: 0;
  outline: 0;
}

h1, h2, h3, h4 {
  margin: 0;
  padding: 0;
}

ul, li, a, p {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button, link {
  cursor: pointer;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.section {
  background-color: var(--back-color-criam);
  margin-left: auto;
  margin-right: auto;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0 15px;
}

@media screen and (min-width: 414px) {
  .container {
    width: 414px;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    max-width: 1024px;
    padding: 0 47px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 85px;
  }
}

.button-up {
  opacity: 0;
  visibility: hidden;
  z-index: 8;
  background-color: var(--color-button);
  border: 2px solid var(--color-text-button);
  width: 50px;
  height: 50px;
  text-align: center;
  color: #494848;
  border-radius: 25px;
  justify-content: center;
  align-items: center;
  font-family: Film P3;
  font-size: 64px;
  font-weight: 700;
  transition: background-color .3s, opacity .5s, visibility .5s;
  animation: animatebut 3s 1s infinite;
  display: inline-flex;
  position: fixed;
  bottom: 50px;
  right: 50px;
}

.button-up:hover, .button-up:focus {
  background-color: var(--color-text-button);
  animation: animatebut step-start;
}

.arrow-up {
  opacity: 0;
  visibility: hidden;
}

.arrow-up span {
  z-index: 3;
  width: 15px;
  height: 15px;
  border-left: 3px solid var(--color-button);
  border-top: 3px solid var(--color-text-button);
  text-shadow: 1px 1px 2px #494848;
  margin: -2px;
  animation: arrow-up 3s infinite;
  display: block;
  position: fixed;
  bottom: 70px;
  right: 60px;
}

.arrow-up span:nth-child(2) {
  animation-delay: -.1s;
  bottom: 90px;
  right: 70px;
}

.arrow-up span:nth-child(3) {
  animation-delay: -.2s;
  bottom: 75px;
  right: 80px;
}

.button-up.button-up-show, .arrow-up.button-up-show {
  opacity: 1;
  visibility: visible;
}

@keyframes arrow-up {
  0% {
    opacity: 0;
    transform: rotate(45deg)translate(10px, 10px);
  }

  50% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: rotate(45deg)translate(-10px, -10px);
  }
}

@keyframes animatebut {
  0% {
    transform: rotateY(0deg);
  }

  50% {
    transform: rotateY(90deg);
  }

  100% {
    transform: rotateY(0deg);
  }
}

@media screen and (max-width: 414px) {
  .button-big-pointed {
    min-width: 100%;
  }
}

@media screen and (min-width: 1024px) {
  .button-up {
    bottom: 50px;
    right: 50px;
  }

  .button-big-pointed {
    min-width: 592px;
    font-size: 24px;
  }
}

@media screen and (min-width: 1280px) {
  .button-big, .button-big-plas {
    min-width: 434px;
    height: 65px;
    font-size: 24px;
  }
}



